import React from 'react'
import Logo from '../../component/logo/logo.js'
import {List,InputItem,Radio, WingBlank, WhiteSpace,Button} from 'antd-mobile'
import { Redirect } from 'react-router-dom';

import './index.css'

import {connect} from 'react-redux';
import { register } from '../../redux/user.redux';
import HocForm from '../../component/hoc-form/hoc-form';
@connect(
  state => state.user,
  {register}
)
@HocForm
class Register extends React.Component {
  constructor(props) {
    super(props)
    this.handleRegister=this.handleRegister.bind(this)
  }
  componentDidMount() {
    this.props.handleChange('type','genius')
  }
  handleRegister(){
    this.props.register(this.props.state)
  }
  render(){
    const RadioItem = Radio.RadioItem
    return (
      <div>
        {this.props.redirectTo?<Redirect to={this.props.redirectTo} />:null}
        <Logo />
        <List>
          {this.props.msg?<p className="error-msg">{this.props.msg}</p>:null}
          <InputItem
            onChange={v=>this.props.handleChange('user',v)}></InputItem>
          <InputItem
            type='password'
            onChange={v=>this.props.handleChange('pwd',v)}></InputItem>
          <InputItem
            type='password'
            onChange={v=>this.props.handleChange('repeatpwd',v)}></InputItem>
          <WhiteSpace />
          <RadioItem checked={this.props.state.type=='genius'}
            onChange={()=>this.props.handleChange('type', 'genius')}>
             </RadioItem>
          <RadioItem checked={this.props.state.type=='boss'}
            onChange={()=>this.props.handleChange('type','boss')}>
            BOSS </RadioItem>
          <WhiteSpace />
          <Button type='primary' onClick={this.handleRegister}></Button>
        </List>
      </div>
    )
  }
}
export default Register;



