import React, { Component } from 'react';
import { connect } from 'react-redux';
import browserCookie from 'browser-cookies';
import { Result,List,WhiteSpace,Modal } from 'antd-mobile';
import { Redirect } from 'react-router-dom';
import { logoutSubmit } from '../../redux/user.redux';

@connect(
  state=>state.user,
  {logoutSubmit}
)
class User extends Component {
  constructor(props){
    super(props)
    this.logout=this.logout.bind(this)
  }
  logout(){
    const alert = Modal.alert
    alert('','',[
      {text: '',onPress:()=>console.log('cancel')},
      {text: '',onPress:()=>{
        browserCookie.erase('userid')
        this.props.logoutSubmit()
      }}
    ])

  }
  render(){
    const props = this.props
    const Item= List.Item
    const Brief= Item.Brief
    return props.user?(
      <div>
        <Result img={<img style={{width: '50px'}} src={require(`../img/${props.avatar}.png`)}
      alt='' />} title={props.user} message={props.type=='boss'?props.company:null} />
      <List renderHeader={()=>''}>
        <Item multipleLine>
          {props.title}
          {props.desc.split('\n').map(v=><Brief key={v}>{v}</Brief>)}
          {props.money?<Brief>{props.money}</Brief>:null}
        </Item>
      </List>
      <WhiteSpace></WhiteSpace>
      <List>
        <Item onClick={this.logout}></Item>
      </List>
      </div>
    ):<Redirect to={this.props.redirectTo} />
  }
}
export default User;



