export function compareObj(obj1,obj2){
  if(obj1==obj2){
    return true
  }
  if(Object.keys(obj1).length!==Object.keys(obj2).length){
    return false
  }
  for(let k in obj1){
    if(typeof obj1[k]=='object')
    {
      return compareObj(obj1[k],obj2[k])
    }
    else if(obj1[k]!==obj2[k])
    {
      return false
    }
  }
  return true
}



